<script lang="ts">
    import { url } from '@sveltech/routify'
    import { Goldhill, boardMetadata } from '@goldhill/Goldhill.ts'
    import ReplyBox from '@goldhill/controls/ReplyBox.svelte'

    export let board

    let thread
    let threadsPromise
    let boardTitle = boardMetadata[board].title
    let catalog = false

    function updateIndex() {
        threadsPromise = Goldhill.getPostsAsync({
            board,
            isOP: true,
        }).then((threads) => threads.sort((a, b) => b.bumpOrder - a.bumpOrder))
    }

    updateIndex()
</script>

<style>

</style>

<site-page id="boardPage" class="text-white">
    <div class="app-body boards-body">
        <div class="container flex-1 items-eft mx-auto select-none">
            <div class="globalMessage hideMobile" id="globalMessage" />
            <div class="boardHeader">
                <div class="boardBanner mt-8">
                    <div class="boardTitle text-2xl text-gray-200">
                        /{board}/ - {boardTitle}
                    </div>
                </div>
            </div>
            {#if thread}
                <a
                    id="startThreadButton"
                    href="#"
                    class="btn-bracket"
                    data-toggle="modal"
                    data-target="#createThreadDialog"
                    data-backdrop="static"
                    data-keyboard="false">
                    Reply to thread
                </a>
            {:else}
                <a
                    id="startThreadButton"
                    href="#"
                    class="btn-bracket"
                    data-toggle="modal"
                    data-target="#createThreadDialog"
                    data-backdrop="static"
                    data-keyboard="false">
                    Start a thread
                </a>
            {/if}
            <hr class="opacity-25 my-1" />
            <span class="navLinks leading-none">
                <a class="btn-bracket" href={$url(`/boards/${board}/`)}>
                    Index
                </a>
                <a class="btn-bracket" href={$url(`/boards/${board}/catalog`)}>
                    Catalog
                </a>
                {#if !thread}
                    <a
                        class="btn-bracket"
                        href={$url(`/boards/${board}/archive`)}>
                        Archive
                    </a>
                {/if}
                {#if thread}
                <a class="btn-bracket" href="#bottom">Bottom</a>
                {/if}
                {#if thread}
                    <a
                        class="btn-bracket"
                        id="index-last-refresh"
                        href="javascript:;">
                        <time
                            title="Last index refresh"
                            data-utc="1548618241000">
                            42&nbsp;seconds&nbsp;ago
                        </time>
                    </a>
                {/if}
                {#if thread}
                    ``
                {:else}
                    <input
                        type="search"
                        id="index-search"
                        class="field webflow-style-input"
                        placeholder="Search" />
                {/if}
            </span>
            <hr class="opacity-25 my-1" />

            {#await threadsPromise then threads}
                <slot scoped={{ threads }} />
            {:catch error}
                <p style="color: red">{error.message}</p>
            {/await}
        </div>
    </div>

    <ReplyBox/>
</site-page>

