<script>
    let urls = [
        'https://i.imgur.com/1JFp7i3.jpg',
        'https://i.imgur.com/L2Wf02i.jpg',
        'https://i.imgur.com/50VCO9S.jpg',
        'https://i.imgur.com/Z106uhL.jpg',
    ]

    let instaPosts = []

    function fetchInsta(name) {
        let url = `https://images${+~~(
            Math.random() * 3333
        )}-focus-opensocial.googleusercontent.com/gadgets/proxy?container=none&url=https://www.instagram.com/${name}/`

        fetch(url)
            .then(async (_) => {
                var regex = /_sharedData = ({.*);<\/script>/m
                var html = await _.text()
                return JSON.parse(regex.exec(html)[1])
            })
            .then((json) => {
                instaPosts = json.entry_data.ProfilePage[0].graphql.user.edge_owner_to_timeline_media.edges.map(
                    (_) => _.node
                )
            })
    }

    fetchInsta('instagram')

    window.test = fetchInsta
</script>

<style>
    .grid .image-entry img {
        transition-property: transform;
        transition-duration: 200ms;
        transition-timing-function: ease-out;
        image-rendering: crisp-edges;
    }

    .grid .image-entry:hover img {
        transform: scale(1.2);
        transition: transform 500ms cubic-bezier(0.5, 1, 0.89, 1);
    }
</style>

