        <h1 class="text-3xl mt-8 text-center sm:text-left">Photos</h1>
        <h2 class="text-gray-400 text-center sm:text-left">Here's some pictures of cats and stuff.</h2>

        <div class="flex flex-col py-12">

            <div class="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-8"> 
                {#each instaPosts as post}
                    <div
                        class="image-entry text-gray-700 text-center rounded-lg
                        overflow-hidden">
                        <a
                            href={`https://www.instagram.com/p/${post.shortcode}/`}
                            target="_blank">
                            <img src={post.thumbnail_src} alt="photo" />
                        </a>
                    </div>
                {/each}
            </div>

        </div>
    </div>
