export let isProduction = false;
export let apiUrl = isProduction ? `https://api.dandev.uk` : `https://apitest.dandev.uk`;
export let graphql = async (query, variables, errorHandler) => {
    return await fetch(`${apiUrl}/graphql`, {
        method: "POST",
        headers: {
            'Content-Type': "application/json",
        },
        credentials: "include",
        body: JSON.stringify({
            query,
            variables,
        }),
    }).then((response) => {
        const contentType = response.headers.get("content-type");
        if (contentType && contentType.indexOf("application/json") !== -1)
            return response.json();
        throw new Error(`Expected JSON response. Status code: ${response.statusText}`);
    }).then((json) => {
        if (json.errors)
            throw new Error(`Query error: ${JSON.stringify(json)}`);
        return { result: json, error: null };
    }).catch(e => {
        console.error(`Failed to query backend API with '${e.props}'`);
        console.error(e);
        if (errorHandler)
            errorHandler(e);
        return { result: null, error: e };
    });
};
