<script lang="ts">
    import { Blog } from '@blog/Blog.ts'

    const shortMonths = [
        'Jan',
        'Feb',
        'Mar',
        'Apr',
        'May',
        'Jun',
        'Jul',
        'Aug',
        'Sep',
        'Oct',
        'Nov',
        'Dec',
    ]

    let postsPromise = Blog.getPostsIndexAsync({ skip: 0, first: 3 })
</script>

<style>
    .post-date {
        color: #b1b1b1;
        font-size: 0.8rem;
        font-weight: 400;
    }

    blog-entry {
        display: inline-block;
        width: 100%;
        height: 5rem;
        overflow: hidden;

        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    @media (min-width: 768px) {
        blog-entry {
            width: 50%;
        }
    }

    @media (min-width: 1024px) {
        blog-entry {
            width: 33%;
        }
    }

    blog-entry:hover {
        background-color: #1a1d1f;
    }
</style>

<post-list class="block">
    {#await postsPromise}
        <span>Loading...</span>
    {:then posts}
        {#each posts.concat(posts).concat(posts) as post}
            <blog-entry class="block rounded-md">
                <a class="flex p-4" href="/blog/{post.slug}">
                    <img class="w-20 rounded-md" src={post.coverImage} />
                    <div class="ml-4 flex flex-col justify-center">
                        <h1 class="text-lg">{post.title}</h1>
                        <span class="post-date" style="opacity: 0.7;">
                            {new Date(post.datePublished).getFullYear()}
                            {shortMonths[new Date(post.datePublished).getMonth()]}
                            {new Date(post.datePublished).getDay()}
                        </span>
                    </div>
                </a>
            </blog-entry>
        {/each}
    {:catch error}
        <p style="color: red">{error.message}</p>
    {/await}
</post-list>
