<script lang="ts">
    import { Blog } from '@blog/Blog.ts'

    const shortMonths = [
        'Jan',
        'Feb',
        'Mar',
        'Apr',
        'May',
        'Jun',
        'Jul',
        'Aug',
        'Sep',
        'Oct',
        'Nov',
        'Dec',
    ]

    export let slug: string

    let html

    let postPromise = Blog.getPostAsync({ slug })

    postPromise.then((post) => (html = post && post.content))

    function getDateStr(dateStr) {
        let date = new Date(dateStr)
        return `${date.getFullYear()} ${shortMonths[date.getMonth()]} ${date.getDay()}`
    }

</script>

