</style>

<site-page id="blogPage" class="text-white">
    <div class="container flex-1 mx-auto">
        <div class="section mt-4">
            {#await postPromise}
                <span>Loading...</span>
            {:then post}
                <h1 class="post-title">{post.title}</h1>

                <div class="mb-4">
                    <span style="color: rgb(180,180,180)">
                        By <a href="/">Daniel Elam</a> <br/>
                    </span>
                    <span style="color: rgb(150,150,150);">
                        Posted <span>{getDateStr(post.datePublished)}</span>
                    </span>
                </div>

                <div
                    class="w-full"
                    style="border-radius: 0.5rem; height: 25rem; max-height:
                    30rem; background-position: 50% 50%; background-repeat:
                    no-repeat; background-size: 100%; background-image: url({post.coverImage});" />


                <div
                    class="blog-content inline"
                    contenteditable="false"
                    bind:innerHTML={html} />
            {:catch error}
