﻿﻿<script lang="ts">
    import { Goldhill, Post } from '@goldhill/Goldhill.ts'
    import PostPresenter from './PostPresenter.svelte'
    import config from '@goldhill/config.ts'

    export let post: Post

    let isThreadPage = false
    let board = 'cs'

    let pd = new Date(post.datePosted) // the time of the post in local timezone
    let postDatePretty = `${pd.getFullYear()}/${pd.getMonth()}/${pd.getDay()} ${pd
        .getHours()
        .toString()
        .padStart(2, '0')}:${pd
        .getMinutes()
        .toString()
        .padStart(2, '0')}:${pd.getSeconds().toString().padStart(2, '0')}`

    function humanisedTimeAgo(time) {
        switch (typeof time) {
            case 'number':
                break
            case 'string':
                time = +new Date(time)
                break
            case 'object':
                if (time.constructor === Date) time = time.getTime()
                break
            default:
                time = +new Date()
        }
        var time_formats = [
            [60, 'seconds', 1], // 60
            [120, '1 minute ago', '1 minute from now'], // 60*2
            [3600, 'minutes', 60], // 60*60, 60
            [7200, '1 hour ago', '1 hour from now'], // 60*60*2
            [86400, 'hours', 3600], // 60*60*24, 60*60
            [172800, 'Yesterday', 'Tomorrow'], // 60*60*24*2
            [604800, 'days', 86400], // 60*60*24*7, 60*60*24
            [1209600, 'Last week', 'Next week'], // 60*60*24*7*4*2
            [2419200, 'weeks', 604800], // 60*60*24*7*4, 60*60*24*7
            [4838400, 'Last month', 'Next month'], // 60*60*24*7*4*2
            [29030400, 'months', 2419200], // 60*60*24*7*4*12, 60*60*24*7*4
            [58060800, 'Last year', 'Next year'], // 60*60*24*7*4*12*2
            [2903040000, 'years', 29030400], // 60*60*24*7*4*12*100, 60*60*24*7*4*12
            [5806080000, 'Last century', 'Next century'], // 60*60*24*7*4*12*100*2
            [58060800000, 'centuries', 2903040000], // 60*60*24*7*4*12*100*20, 60*60*24*7*4*12*100
        ]
        var seconds = (+new Date() - time) / 1000,
            token = 'ago',
            list_choice = 1

        if (seconds === 0) {
            return 'Just now'
        }
        if (seconds < 0) {
            seconds = Math.abs(seconds)
            token = 'from now'
            list_choice = 2
        }
        var i = 0,
            format
        while ((format = time_formats[i++]))
            if (seconds < format[0]) {
                if (typeof format[2] == 'string') return format[list_choice]
                else
                    return (
                        Math.floor(seconds / format[2]) +
                        ' ' +
                        format[1] +
                        ' ' +
                        token
                    )
            }
        return time
    }

    postDatePretty = humanisedTimeAgo(pd)

    async function toggleInlineReply(replyId) {
        let index = post.openReplies.indexOf(replyId)
        if (index > -1) post.openReplies.splice(index, 1)
        else post.openReplies.push(replyId)
        post.openReplies = post.openReplies
    }

    function toCodePoints(str) {
        var arCP = []
        for (var i = 0; i < str.length; i += 1) {
            var cP = str.codePointAt(i)
            arCP.push(cP)
            if (cP >= 0x10000) {
                i += 1
            }
        }
        return arCP
    }

    function isoCountryCodeToFlagEmoji(country) {
        if (country == 'GB-SCT') {
            return String.fromCodePoint(
                0x1f3f4,
                0xe0067,
                0xe0062,
                0xe0073,
                0xe0063,
                0xe0074,
                0xe007f
            )
        }
        return String.fromCodePoint(
            ...[...country.toUpperCase()].map((c) => c.charCodeAt() + 0x1f1a5)
        )
    }

    function flagEmojiToFileName(emoji) {
        return `${toCodePoints(emoji)
            .map((e) => `u${e.toString(16).toUpperCase()}`)
            .join('_')}.png`
    }

    function hashCode(str) {
        let hash = 0
        for (var i = 0; i < str.length; i++) {
            hash = str.charCodeAt(i) + ((hash << 5) - hash)
        }
        return hash
    }

    function handPaint(str) {
        return `hsl(${hashCode(str) % 360}, 20%, 40%)`
    }
</script>

<style>
    :global(.inline post-presenter) {
        margin-bottom: 1.5rem;
    }
    :global(.inline post-content) {
        padding-top: 0.5rem;
        padding-bottom: 0.5rem;
        background-color: rgba(0, 0, 0, 0.1);
    }
</style>

<div 
    class="post-info {post.parentThread == null ? 'mt-2' : ''}"
    id={`pi${post.postNumber}`}
    style={post.parentThread && 'margin-top: -2px;'}>
    <a class="watch-thread-link" title="Watch Thread" />
    <span class="subject anim-text-flow font-bold" style="color: #b19993">
        {post.subject || ''}
    </span>
    {post.subject !== undefined ? ' ' : ''}
    <span class="nameBlock">
        {#if post.admin}
            <span class="name-admin">[ADMIN]</span>
        {/if}
        <span class="name font-medium">{post.name || config.anonName}</span>
        <span
            class="poster-id inline-flex cursor-pointer"
            style="font-size: 0.76rem;"
            title="This ID is unique to this poster for this thread.">
            (ID:
            <span
                class="ml-1 hand-painted rounded-sm text-xs"
                style="padding: 0.05rem 0.15rem; margin-top: -0.05rem;
                background-color:{handPaint(post.ipHash)}">
                {post.ipHash}
            </span>
            )
        </span>
        <img
            class="inline-block w-6 h-6"
            title={post.country ?? 'Unknown'}
            alt={post.country}
            src={`/i/flags/${post.country == null ? 'u1F3F3.png' : flagEmojiToFileName(isoCountryCodeToFlagEmoji(post.country))}`} />
    </span>
    <span class="dateTime" style="color: #b3b3b3; font-size: 0.85rem">
        {postDatePretty}
    </span>
    <span class="postNum desktop inline-flex">
        <a
            href={isThreadPage ? `#p${post.postNumber}` : `/boards/${board}/thread/${post.parentThread || post.postNumber}#p${post.postNumber}`}
            title="Link to this post">
            No.
        </a>
        <a
            href={isThreadPage ? `#q${post.postNumber}` : `/boards/${board}/thread/${post.parentThread || post.postNumber}#q${post.postNumber}`}
            title="Reply to this post">
            {post.postNumber.toString().padStart(4, '0')}
        </a>
    </span>
    <span>{` `}</span>
    <a class="menu-button">
        <i class="fa fa-angle-down" />
    </a>
    <span class="replies">
        {#each post.replies as reply}
            <a role="button" on:mousedown={() => toggleInlineReply(reply)}>
                &gt;&gt;{reply}
            </a>
        {/each}
    </span>
    <div class="inline">
        {#each post.openReplies as replyId}
            {#await Goldhill.getPostByIdAsync(replyId) then reply}
                <PostPresenter inline={true} post={reply} />
            {/await}
        {/each}
    </div>
</div>
