﻿<script lang="ts">
    import { Goldhill, Post } from '@goldhill/Goldhill.ts'
    import config from '@goldhill/config.ts'
    import PostPresenter from './PostPresenter.svelte'

    export let thread: Post
    let visibleReplies = []
    let replies = []
    let imagesOmitted = 0
    let postsOmitted = 0
    let postTotal = 0
    let imageTotal = 0
    let summarized = true
    let collapsed = false
    let isThreadPage = false

    function sliceIfNonZero(self, value) {
        if (value === 0 || value === -0) return self
        return self.slice(value)
    }

    async function fetchRepliesAsync() {      
        replies = await Goldhill.getPostsAsync({ parent: thread.postNumber })
    }   
 
    function updateReplies() {
        //const thread = params;

        thread.children = []
        thread.replies = []

        const postLookup = replies.reduce(function (map, post) {
            map[post.postNumber] = post
            return map
        }, {})

        for (let post of replies) {
            post.replies = []
            thread.children.push(post)

            for (let replyNum of post.repliedTo) {
                const repliedPost = postLookup[replyNum]
                if (repliedPost) repliedPost.replies.push(post.postNumber)
            }
        }

        /* -------------------------------------- */

        imagesOmitted = 0
        postsOmitted = 0
        let postCount = thread.children.length
        let maxShown = summarized ? config.indexReplyCount : postCount

        if (thread.children.length > maxShown && summarized) {
            for (let i = 0; i < postCount - maxShown; i++) {
                if (thread.children[i].image != null) imagesOmitted++
                else postsOmitted++
            }
        }

        console.log(thread.children.length, maxShown)

        let visibleReplies2 = thread.children.slice(0)
        visibleReplies2 = sliceIfNonZero(replies, -maxShown)
        visibleReplies2.splice(0, 0, thread)

        visibleReplies = visibleReplies2

        if (!isThreadPage) {
            postTotal = 0
            imageTotal = 0

            for (let child of replies) {
                if (child.image !== null) imageTotal++
                else postTotal++
            }
        }
    }

    fetchRepliesAsync().then(updateReplies)
</script>

<style>
    .board-entry {
        height: calc(100vh * 0.6);
    }

    .board-entry .board-title {
        text-align: center;
    }

    .board-entry span {
        text-shadow: 0px 0px 4px #00000041;
    }
</style>

<thread-presenter
    class="select-text {collapsed ? `thread threadCollapsed` : `thread`}">
    {#each visibleReplies.map((post, index) => {
        return { post, index }
    }) as { post, index }}
        {#if index === 0}
            <PostPresenter
                op={true}
                {post}
                {thread}
                {collapsed}
                {summarized}
                summaryInfo={{ imagesOmitted, postsOmitted, postTotal, imageTotal }}
                toggleCollapsed={() => (collapsed = !collapsed)}
                onExpand={() => {
                    summarized = !summarized
                    updateReplies()
                }} />
        {:else if !collapsed}
            <PostPresenter
                op={false}
                {post}
                {thread}
                summaryInfo={{ imagesOmitted, postsOmitted, postTotal, imageTotal }}
                onExpand={null} />
        {/if}
    {/each}
</thread-presenter>
